/* ToolTip */
.tooltip {
  position: relative;
  display: inline-block;
  color:  rgb(165, 88, 88) ;
  border-bottom: 1px double;
  cursor:alias;
}

.tooltip .tooltiptext {
  visibility: hidden;
  opacity: 0;
  width: 300px;
  background-color: rgba(255, 255, 255, 0.705);
  color: #000000;
  text-align: center;
  border: #000000 solid 1px;
  padding: 2px 0;
  transition: 0.27s;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 105%;

}
.tooltiptext{
  width: 300px;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
  cursor:default;

  
}
